/* global jQuery */
void (function initializeCustomScroll($) {
	'use strict';

	const initCopyText = function initCopyText(wrapper) {
		const $wrapper = $(wrapper);
		const textToCopy = $(wrapper).parent().text();
		const copyMessageVisibleTime = 1000;

		$wrapper.attr('data-clipboard-text', textToCopy);

		$wrapper.click(() => {
			$wrapper.attr('data-copy', 'copied');

			setTimeout(() => {
				$wrapper.attr('data-copy', '');
			}, copyMessageVisibleTime);
		});

		/* eslint-disable */
		new Clipboard($wrapper[0]);
		/* eslint-enable */

		return;
	};

	$.fn.copyText = function init() {
		return this.each((index, wrapper) => {
			initCopyText($(wrapper));
		});
	};

	$('[data-function="copy"]').copyText();
})(jQuery);
